import React from "react";
import { Link } from "gatsby";

import "./styles.scss";

const Card = ({ data }) => {
    const {frontmatter} = data;
    return (
    <Link to={`/${frontmatter.slug}`} className="articleCard">
        <img src={frontmatter.cover_photo} alt={frontmatter.title} />
        <div>{frontmatter.title}</div>
        <div>{frontmatter.date}</div>
    </Link>
    );
};

export default Card;